let _compData = {
  '_toast_.isHide': false,// 
  '_toast_.content': ''// 
}
let toastPannel = {
  // toast
  toastSuccess: function (data) {
    let self = this;
    this.setData({ '_toast_.isHidescss': true, '_toast_.content': data });
    setTimeout(function () {
      self.setData({ '_toast_.isHidescss': false })//
    }, 2000)
  },

   toastFilled: function (data) {
    let self = this;
    this.setData({ '_toast_.isHidefil': true, '_toast_.content': data });
    setTimeout(function () {
      self.setData({ '_toast_.isHidefil': false })//
    }, 2000)
  }
}

function ToastPannel() {//
  // 
  let pages = getCurrentPages();
  let curPage = pages[pages.length - 1];
  this.__page = curPage;
  // 
  Object.assign(curPage, toastPannel);
  // page
  curPage.toastPannel = this;
  // data
  curPage.setData(_compData);
  return this;
}

module.exports = {
  ToastPannel
}